.Carousel {
  width: 650px;
  margin: auto;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  &__list {
    width: 260px;
    overflow: hidden;
  }

  &__container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

  }

  &__items {
    width: 1300px;
    height: 130px;

    display: flex;
    justify-content: center;
    flex-direction: row;
    list-style-type: none;
  }
}

.button {
  width: 24px;
  height: auto;

  font-size: 24px;
  font-weight: 700;
  line-height: 100%;
  text-align: center;

  border-radius: 50%;

  transition: all 0.3s ease;

  &:hover {
    background-color: grey;
    color: #fff;
  }

  &-next {
    padding-left: 2px;
  }

  &-prev {
    padding-right: 2px;
  }
}

.inputs {
  &__container {
    display: flex;
    flex-direction: column;
  }

}
